function toggleClass(elem, classString) {
  elem.classList.toggle(classString);
}

function toggleHeaderState(obj, key) {
  const objectToggle = obj;
  objectToggle[key] = !obj[key];
}

function listenersFunc(e) {
  const { target } = e;
  if (target.closest('.btn_menu')) {
    toggleClass(target.closest('.btn_menu'), 'open');
    toggleHeaderState(this, 'isMenuOpen');
  }
  if (target.closest('.btn_options')) {
    toggleClass(target.closest('.btn_options'), 'open');
    toggleHeaderState(this, 'isOptionsOpen');
  }
}

export default class Header {
  constructor(props) {
    this.props = props;
  }

  static create() {
    const header = document.createElement('header');
    const wrapper = document.createElement('div');
    const btnMenu = document.createElement('button');
    const btnOptions = document.createElement('button');
    const h1 = document.createElement('h1');
    wrapper.classList.add('wrapper');
    btnMenu.classList.add('btn', 'btn_menu');
    btnOptions.classList.add('btn', 'btn_options');
    h1.innerText = 'Piskel Clone';
    btnMenu.innerHTML = '<div class="line line_1"></div>'
                      + '<div class="line line_2"></div>'
                      + '<div class="line line_3"></div>';
    btnOptions.innerHTML = '<div class="dot dot_1"></div>'
                         + '<div class="dot dot_2"></div>'
                         + '<div class="dot dot_3"></div>';
    wrapper.appendChild(btnMenu);
    wrapper.appendChild(h1);
    wrapper.appendChild(btnOptions);
    header.appendChild(wrapper);
    return header;
  }

  render() {
    const { props } = this;
    const header = Header.create();
    header.addEventListener('click', listenersFunc.bind(props), false);
    document.body.appendChild(header);
  }
}
