export default class Frame {
  constructor(props) {
    this.props = props;
  }

  static createFrameShadow() {
    const frameShadow = document.createElement('li');
    frameShadow.classList.add('frame', 'frame_shadow');
    return frameShadow;
  }

  static create(number) {
    const frame = document.createElement('li');
    const frameNumb = document.createElement('div');
    const frameRemove = document.createElement('button');
    const frameCopy = document.createElement('button');
    const frameMove = document.createElement('div');
    const frameCanvas = document.createElement('div');
    const canvasPreview = document.createElement('canvas');
    frameRemove.setAttribute('title', 'shortcut: "R"');
    frameCopy.setAttribute('title', 'shortcut: "V"');
    canvasPreview.setAttribute('width', '32');
    canvasPreview.setAttribute('height', '32');
    frameNumb.innerHTML = `<span>${number}</span>`;
    frame.setAttribute('data-number', number);
    frame.classList.add('frame');
    frameNumb.classList.add('frame__number');
    frameRemove.classList.add('frame__remove');
    frameCopy.classList.add('frame__copy');
    frameMove.classList.add('frame__move');
    frameCanvas.classList.add('frame__canvas');
    frameCanvas.appendChild(canvasPreview);
    frame.appendChild(frameNumb);
    frame.appendChild(frameRemove);
    frame.appendChild(frameCopy);
    frame.appendChild(frameMove);
    frame.appendChild(frameCanvas);
    return frame;
  }

  render() {
    const { countFrames } = this.props;
    const frame = Frame.create(countFrames + 1);
    const button = document.querySelector('.btn_new-frame');
    document.querySelector('.frames').insertBefore(frame, button);
  }
}
