export default class ToGif {
  static toggleVisibleToGif(countFrames) {
    const toGif = document.querySelector('.preview__to-gif');
    if (countFrames === 0) {
      toGif.classList.remove('show');
    } else {
      toGif.classList.add('show');
    }
  }

  static createButton() {
    const button = document.createElement('button');
    button.innerText = 'get GIF';
    button.classList.add('preview__to-gif');
    return button;
  }
}
