import Frame from './frame/frame';
import Canvas from '../../screens/canvas/canvas';
import Preview from '../../screens/preview/preview';
import CanvasOptions from '../../screens/canvas/canvas-options/canvas-options';

function isEmptyFrames(count) {
  return count === 0;
}

function setFramesCorrectNumb() {
  if (!document.querySelectorAll('.frame')) return;
  document.querySelectorAll('.frame').forEach((item, index) => {
    const frameNumber = item.querySelector('.frame__number span');
    frameNumber.innerText = index + 1;
    item.setAttribute('data-number', index + 1);
  });
}

function changeCountFrame(action, props) {
  const playerProperty = props;
  if (action === 'add') playerProperty.countFrames += 1;
  if (action === 'remove') {
    if (!isEmptyFrames(playerProperty.countFrames)) {
      playerProperty.countFrames -= 1;
    } else {
      playerProperty.countFrames = 0;
    }
  }
}

function createNode(props) {
  const frame = new Frame(props);
  frame.render();
}

function removeNode(elem, parentSelector) {
  document.querySelector(parentSelector).removeChild(elem);
}

function copyNode(elem, parentSelector) {
  const newFrame = elem.cloneNode(true);
  const prevElem = elem.nextSibling;
  document.querySelector(parentSelector).insertBefore(newFrame, prevElem);
}

function changeCurrFrameProp(numb, props) {
  const playerProps = props;
  playerProps.currentFrame = numb;
}

function removeActiveClass(elemSelector) {
  const activeFrame = document.querySelector(`${elemSelector}.active`);
  if (activeFrame) activeFrame.classList.remove('active');
}

function setActiveFrame(elem, props) {
  const frameNumb = +elem.getAttribute('data-number');
  removeActiveClass('.frame');
  elem.classList.add('active');
  changeCurrFrameProp(frameNumb, props);
}

function createFrame(button, props) {
  createNode(props);
  changeCountFrame('add', props);
  const newFrame = button.previousSibling;
  setActiveFrame(newFrame, props);
}

function removeFrame(frame, props) {
  const playerProps = props;
  const prevElem = frame.previousSibling;
  const nextElem = frame.nextSibling;
  removeNode(frame, '.frames');
  changeCountFrame('remove', playerProps);
  setFramesCorrectNumb();
  if (frame.classList.contains('active')) {
    const activeFrame = prevElem || nextElem;
    if (activeFrame && !isEmptyFrames(playerProps.countFrames)) {
      setActiveFrame(activeFrame, playerProps);
    }
  } else {
    const activeFrame = document.querySelector('.frame.active');
    setActiveFrame(activeFrame, playerProps);
  }
  if (isEmptyFrames(playerProps.countFrames)) {
    playerProps.currentFrame = null;
  }
}

function copyFrame(frame, props) {
  copyNode(frame, '.frames');
  changeCountFrame('add', props);
  setFramesCorrectNumb();
  const newFrame = frame.nextSibling;
  setActiveFrame(newFrame, props);
}

function createCanvas(props) {
  const canvas = new Canvas(props);
  canvas.render();
}

function removeCanvas(frame) {
  const removeFrameNumb = frame.getAttribute('data-number');
  const canvas = document.querySelector(`[data-canvas-number='${removeFrameNumb}']`);
  removeNode(canvas, '.canvas-list');
  Canvas.setCanvasCorrectNumb();
}

function copyCanvasListItem(frame) {
  const copyFrameNumb = frame.getAttribute('data-number');
  const canvasWrapper = document.querySelector(`[data-canvas-number='${copyFrameNumb}']`);
  const canvas = document.querySelector(`[data-canvas-number='${copyFrameNumb}']`).firstChild;
  copyNode(canvasWrapper, '.canvas-list');
  const newCanvas = document.querySelectorAll(`[data-canvas-number='${copyFrameNumb}']`)[1].firstChild;
  CanvasOptions.setCorrectSize(newCanvas);
  Canvas.copyCanvas(canvas, newCanvas);
  Canvas.setCanvasCorrectNumb();
}

function setActiveCanvas() {
  const activeFrame = document.querySelector('.frame.active');
  if (!activeFrame) return;
  const activeFrameNumb = activeFrame.getAttribute('data-number');
  const canvasWrapper = document.querySelector(`[data-canvas-number='${activeFrameNumb}']`);
  removeActiveClass('.canvas-list__item');
  canvasWrapper.classList.add('active');
}

function appendFrameShadow(prevNode) {
  const frameShadow = Frame.createFrameShadow();
  const nextNode = prevNode.nextSibling;
  const framesList = prevNode.parentNode;
  framesList.insertBefore(frameShadow, nextNode);
}

function removeFrameShadow() {
  const frameShadow = document.querySelector('.frame_shadow');
  frameShadow.parentNode.removeChild(frameShadow);
}

function setCoordinates(frame) {
  const draggableFrame = frame;
  const framesList = draggableFrame.parentNode;
  const framesListStyle = window.getComputedStyle(framesList);
  const frameTop = draggableFrame.getBoundingClientRect().top;
  const correction = {
    top: frameTop - parseInt(framesListStyle.top, 10) + framesList.scrollTop || 0,
  };
  draggableFrame.style.setProperty('top', `${correction.top}px`);
}

function replaceFrameListItems(removedNodeNumb, beforeNodeNumb) {
  const canvasList = document.querySelector('.canvas-list');
  const removedNode = document.querySelector(`[data-canvas-number='${removedNodeNumb}']`);
  const beforeNode = document.querySelector(`[data-canvas-number='${beforeNodeNumb}']`);
  const removedCanvasItem = canvasList.removeChild(removedNode);
  canvasList.insertBefore(removedCanvasItem, beforeNode);
}

function checkReplace(nodeArray, frame) {
  const prevNode = frame.previousSibling ? frame.previousSibling : null;
  const nextNode = frame.nextSibling.nextSibling.classList.contains('frame') ? frame.nextSibling.nextSibling : null;
  if (nodeArray.includes(prevNode)) return prevNode;
  if (nodeArray.includes(nextNode)) return nextNode;
  return null;
}

function isReplaceWithTop(movedNode, replacedNode) {
  const topNode = movedNode.previousSibling;
  return replacedNode === topNode;
}

function replaceFrameItems(draggableFrame, nodeForReplace) {
  const frameList = draggableFrame.parentNode;
  const shadowNode = document.querySelector('.frame_shadow');
  if (isReplaceWithTop(draggableFrame, nodeForReplace)) {
    const removedNode = frameList.removeChild(nodeForReplace);
    const removedNodeNumb = removedNode.getAttribute('data-number');
    const beforeNodeNumb = shadowNode.nextSibling.getAttribute('data-number');
    frameList.insertBefore(removedNode, shadowNode.nextSibling);
    replaceFrameListItems(removedNodeNumb, beforeNodeNumb);
  } else {
    const removedNode = frameList.removeChild(nodeForReplace);
    const removedNodeNumb = removedNode.getAttribute('data-number');
    const beforeNodeNumb = draggableFrame.getAttribute('data-number');
    frameList.insertBefore(removedNode, draggableFrame);
    replaceFrameListItems(removedNodeNumb, beforeNodeNumb);
  }
}

function moveFrameItem(e, frame, frameTop) {
  const draggableFrame = frame;
  const startY = draggableFrame.getAttribute('data-start-y');
  const endY = e.clientY;
  const resultMove = +endY - +startY;
  const nodesArray = document.elementsFromPoint(e.clientX, e.clientY);
  const nodeForReplace = checkReplace(nodesArray, frame);
  if (nodeForReplace) replaceFrameItems(draggableFrame, nodeForReplace, e);
  draggableFrame.style.top = `${frameTop + resultMove}px`;
}

function endMove(e) {
  const draggableFrame = document.querySelector('.draggable');
  const playerProps = window.player;
  // eslint-disable-next-line no-use-before-define
  removeListeners(e);
  removeFrameShadow();
  draggableFrame.setAttribute('style', '');
  draggableFrame.classList.remove('draggable');
  setFramesCorrectNumb();
  Canvas.setCanvasCorrectNumb();
  Preview.createAnimationFrames(playerProps);
}

function removeListeners(e) {
  const framesList = e.target.closest('.frames');
  framesList.onmousemove = null;
  framesList.removeEventListener('mouseleave', endMove);
  framesList.removeEventListener('mouseup', endMove);
}

function listenersFunc(e) {
  const { target } = e;
  const that = this;
  const eventType = e.type;
  if (target.closest('.btn_new-frame') && eventType === 'click') {
    createFrame(target.closest('.btn_new-frame'), that);
    createCanvas(that);
    setActiveCanvas();
    Preview.createAnimationFrames(that, 'add');
  }
  if (target.closest('.frame__remove') && eventType === 'click') {
    removeFrame(target.closest('.frame'), that);
    removeCanvas(target.closest('.frame'));
    setActiveCanvas();
    Preview.createAnimationFrames(that);
    return;
  }
  if (target.closest('.frame__copy') && eventType === 'click') {
    copyFrame(target.closest('.frame'), that);
    copyCanvasListItem(target.closest('.frame'), that);
    setActiveCanvas();
    Preview.createAnimationFrames(that);
    return;
  }
  if (target.closest('.frame') && eventType === 'click') {
    setActiveFrame(target.closest('.frame'), that);
    setActiveCanvas();
  }
  if (target.closest('.frame__move') && eventType === 'mousedown') {
    const frame = target.closest('.frame');
    const framesList = target.closest('.frames');
    appendFrameShadow(frame);
    setCoordinates(frame, e);
    const frameTop = parseInt(frame.style.top, 10);
    frame.classList.add('draggable');
    frame.setAttribute('data-start-y', e.clientY);
    framesList.onmousemove = (ev) => {
      moveFrameItem(ev, frame, frameTop);
    };
    framesList.addEventListener('mouseleave', endMove);
    framesList.addEventListener('mouseup', endMove);
  }
}

export default class FramesList {
  constructor(props) {
    this.props = props;
  }

  static create() {
    const framesList = document.createElement('ul');
    const addNewFrame = document.createElement('button');
    framesList.classList.add('frames');
    addNewFrame.classList.add('btn_new-frame');
    addNewFrame.setAttribute('title', 'shortcut: "F"');
    addNewFrame.innerHTML = '<div class="icon"></div><span>Add new frame</span>';
    framesList.appendChild(addNewFrame);
    return framesList;
  }

  render() {
    const { props } = this;
    const framesList = FramesList.create();
    framesList.addEventListener('click', listenersFunc.bind(props));
    framesList.addEventListener('mousedown', listenersFunc.bind(props));
    document.body.appendChild(framesList);
  }
}
