import GIF from 'gif.js.optimized';
import ToGif from './gif/gif';
import Worker from '../../../node_modules/gif.js.optimized/dist/gif.worker';

function fixBlackColorInGif(image) {
  const correctImage = image;
  for (let i = 3; i < correctImage.data.length; i += 4) {
    if (correctImage.data[i] === 0) {
      correctImage.data[i - 1] = 255;
      correctImage.data[i - 2] = 255;
      correctImage.data[i - 3] = 255;
      correctImage.data[i] = 255;
    }
  }
  return correctImage;
}

function getImageData(canvas) {
  const ctx = canvas.getContext('2d');
  const canvasSize = canvas.getAttribute('height');
  return ctx.getImageData(0, 0, canvasSize, canvasSize);
}

function getMaxSizeCanvas(canvas, maxSize) {
  const imageData = getImageData(canvas);
  const newCanvas = document.createElement('canvas');
  newCanvas.setAttribute('width', maxSize);
  newCanvas.setAttribute('height', maxSize);
  const ctx = newCanvas.getContext('2d');
  ctx.putImageData(imageData, 0, 0);
  return newCanvas;
}

function getMaxFrameSize(frames) {
  let maxSize = 0;
  frames.forEach((item) => {
    const canvas = item.querySelector('canvas');
    const canvasSize = canvas.width;
    if (maxSize < canvasSize) maxSize = canvasSize;
  });
  return maxSize;
}

function clearTimeOut() {
  while (window.intAnimPrev > 0) {
    window.clearTimeout(window.intAnimPrev);
    window.intAnimPrev -= 1;
  }
}

function openInFullScreen() {
  const element = document.querySelector('.preview__animation');
  if (element.requestFullscreen) {
    element.requestFullscreen();
  } else if (element.mozRequestFullScreen) {
    element.mozRequestFullScreen();
  } else if (element.webkitRequestFullscreen) {
    element.webkitRequestFullscreen();
  } else if (element.msRequestFullscreen) {
    element.msRequestFullscreen();
  }
}

function clearAnimationFrames(props) {
  const playerProperty = props;
  playerProperty.animationFrames.length = 0;
}

function checkNextIndexOutRange(props) {
  return props.frameOnPreview + 1 > props.animationFrames.length - 1;
}

function writeFPS(fps) {
  const fpsView = document.querySelector('.preview__fps-view');
  fpsView.innerText = `FPS: ${fps}`;
}

function changeFPS(evTarget, props) {
  const that = props;
  const evValue = +evTarget.value;
  that.selectedFPS = evValue;
  writeFPS(evValue);
}

function changePreviewBg(image) {
  const animationBlock = document.querySelector('.preview__animation');
  animationBlock.style.setProperty('background-image', `url("${image}")`);
}

function clearBackgroundPreview() {
  const animationBlock = document.querySelector('.preview__animation');
  animationBlock.style.setProperty('background-image', 'url("")');
}

function animationPreview(props) {
  const playerProperty = props;
  clearTimeOut();
  if (playerProperty.animationFrames.length === 0) {
    clearBackgroundPreview();
    return;
  }
  const imageOnPreview = playerProperty.animationFrames[playerProperty.frameOnPreview];
  changePreviewBg(imageOnPreview);
  if (checkNextIndexOutRange(playerProperty)) {
    playerProperty.frameOnPreview = 0;
  } else {
    playerProperty.frameOnPreview += 1;
  }
  const time = 1000 / playerProperty.selectedFPS;
  window.intAnimPrev = setTimeout(animationPreview, time, playerProperty);
}

function listenerFunc(e) {
  const { target } = e;
  const evType = e.type;
  const that = this;
  switch (evType) {
    case 'input': {
      const inputRange = target.closest('.preview__fps-control');
      changeFPS(inputRange, that);
      break;
    }
    case 'click': {
      if (target.closest('.preview__fullscreen')) {
        openInFullScreen();
      }
      if (target.closest('.preview__to-gif')) {
        const canvases = document.querySelectorAll('.frame__canvas');
        const worker = new Worker();
        const maxSize = getMaxFrameSize(canvases);
        if (worker) {
          const gif = new GIF({
            workers: 2,
            quality: 10,
            width: maxSize,
            height: maxSize,
          });
          canvases.forEach((element) => {
            let canvas = element.querySelector('canvas');
            const canvasSize = canvas.width;
            if (canvasSize < maxSize) {
              canvas = getMaxSizeCanvas(canvas, maxSize);
            }
            let image = canvas.getContext('2d').getImageData(0, 0, maxSize, maxSize);
            image = fixBlackColorInGif(image);
            gif.addFrame(image, { delay: 1000 / that.selectedFPS });
          });
          gif.on('finished', (ev) => {
            const downloadLink = document.createElement('a');
            const resultGif = URL.createObjectURL(ev);
            downloadLink.setAttribute('href', resultGif);
            downloadLink.setAttribute('download', 'download');
            downloadLink.click();
          });
          gif.render();
        }
      }
      break;
    }
    default: {
      break;
    }
  }
}

export default class Preview {
  constructor(props) {
    this.props = props;
  }

  static create(props) {
    const fpsValue = props.selectedFPS;
    const previewWrapper = document.createElement('div');
    const previewSettings = document.createElement('div');
    const fpsRange = document.createElement('input');
    const fpsView = document.createElement('span');
    const animation = document.createElement('div');
    const fullScreen = document.createElement('button');
    const toGif = ToGif.createButton();
    fpsView.innerText = `FPS: ${fpsValue}`;
    fpsRange.setAttribute('type', 'range');
    fpsRange.setAttribute('min', '1');
    fpsRange.setAttribute('max', '24');
    fpsRange.setAttribute('step', '1');
    fpsRange.value = fpsValue;
    previewWrapper.classList.add('preview');
    previewSettings.classList.add('preview__settings');
    fpsRange.classList.add('preview__fps-control');
    fpsView.classList.add('preview__fps-view');
    animation.classList.add('preview__animation');
    fullScreen.classList.add('preview__fullscreen');
    previewSettings.appendChild(fpsView);
    previewSettings.appendChild(fpsRange);
    previewWrapper.appendChild(fullScreen);
    previewWrapper.appendChild(toGif);
    previewWrapper.appendChild(animation);
    previewWrapper.appendChild(previewSettings);
    return previewWrapper;
  }

  static createAnimationFrames(props, flag = 'rebuild') {
    const playerProperty = props;
    const imagesArr = [];
    const canvasList = document.querySelectorAll('.canvas-list li');
    const activeCanvasWrapper = document.querySelector('.canvas-list li.active');
    if (!activeCanvasWrapper) {
      playerProperty.animationFrames.length = 0;
      ToGif.toggleVisibleToGif(playerProperty.animationFrames.length);
      return;
    }
    const activeCanvas = activeCanvasWrapper.firstChild;
    if (flag === 'add') {
      playerProperty.animationFrames.push(activeCanvas.toDataURL());
    } else if (flag === 'rebuild') {
      canvasList.forEach((item) => {
        const canvas = item.firstChild;
        imagesArr.push(canvas.toDataURL());
      });
      clearAnimationFrames(props);
      playerProperty.animationFrames = imagesArr;
    }
    ToGif.toggleVisibleToGif(playerProperty.animationFrames.length);
  }

  static startAnimationPreview(props) {
    const time = 1000 / props.selectedFPS;
    window.intAnimPrev = setTimeout(animationPreview, time, props);
  }

  render() {
    const { props } = this;
    const preview = Preview.create(props);
    preview.addEventListener('input', listenerFunc.bind(props));
    preview.addEventListener('click', listenerFunc.bind(props));
    document.body.appendChild(preview);
  }
}
