import Preview from '../../screens/preview/preview';

function toggleClass(elem, classString) {
  elem.classList.toggle(classString);
}

function cleanCanvas(canvas) {
  const canvasSize = canvas.getAttribute('height');
  const ctx = canvas.getContext('2d');
  ctx.clearRect(0, 0, canvasSize, canvasSize);
}

function writeSize(size) {
  const penSizeView = document.querySelector('.pen-size_view');
  penSizeView.innerHTML = `<b>Pen size:</b> ${size}`;
}

function changePenSize(evTarget, props) {
  const that = props;
  const evValue = +evTarget.value;
  that.canvasState.penSize = evValue;
  writeSize(evValue);
}

function changeColor(props) {
  const that = props;
  const primaryColorBlock = document.querySelector('.color_primary');
  const secondaryColorBlock = document.querySelector('.color_secondary');
  const colorPrimary = getComputedStyle(primaryColorBlock).backgroundColor;
  const secondaryColor = getComputedStyle(secondaryColorBlock).backgroundColor;
  primaryColorBlock.style.backgroundColor = secondaryColor;
  secondaryColorBlock.style.backgroundColor = colorPrimary;
  that.canvasState.currentColor = secondaryColor;
}

function isSelfClick(obj, selfKey) {
  return obj[selfKey];
}

function disableActiveTool(obj) {
  const toolsObj = obj;
  Object.keys(toolsObj).forEach((key) => {
    toolsObj[key] = false;
  });
}

function removeActiveClass() {
  const activeTool = document.querySelector('.tool.active');
  if (activeTool) activeTool.classList.remove('active');
}

function toggleToolsState(obj, key) {
  const toolsObj = obj;
  toolsObj[key] = !obj[key];
}

function toggleTools(element, nameState) {
  const { selectedTools } = window.player;
  if (!isSelfClick(selectedTools, nameState)) {
    disableActiveTool(selectedTools);
    removeActiveClass();
  }
  toggleClass(element, 'active');
  toggleToolsState(selectedTools, nameState);
}

function listenersFunc(e) {
  const that = this;
  const { target: evTarget, type: evType } = e;
  if (evTarget.closest('.tool_paint-bucket')) {
    toggleTools(evTarget.closest('.tool_paint-bucket'), 'bucket');
  }
  if (evTarget.closest('.tool_color-picker')) {
    toggleTools(evTarget.closest('.tool_color-picker'), 'colorPicker');
  }
  if (evTarget.closest('.tool_move')) {
    toggleTools(evTarget.closest('.tool_move'), 'move');
  }
  if (evTarget.closest('.tool_lighten-darken')) {
    toggleTools(evTarget.closest('.tool_lighten-darken'), 'lightenDarken');
  }
  if (evTarget.closest('.tool_pen')) {
    toggleTools(evTarget.closest('.tool_pen'), 'pen');
  }
  if (evTarget.closest('.tool_eraser')) {
    toggleTools(evTarget.closest('.tool_eraser'), 'eraser');
  }
  if (evTarget.closest('.tool_line')) {
    toggleTools(evTarget.closest('.tool_line'), 'line');
  }
  if (evTarget.closest('.tool_clean')) {
    if (!document.querySelector('.frame')) return;
    const message = '    .      ?';
    const userAnswer = window.confirm(message);
    if (userAnswer) {
      const canvas = document.querySelector('.canvas-list__item.active canvas');
      const frame = document.querySelector('.frame.active canvas');
      cleanCanvas(canvas);
      cleanCanvas(frame);
      Preview.createAnimationFrames(that);
    }
  }
  if (evTarget.closest('.tool_ellipse')) {
    toggleTools(evTarget.closest('.tool_ellipse'), 'ellipse');
  }
  if (evTarget.closest('.tool_ellipse__paint')) {
    toggleTools(evTarget.closest('.tool_ellipse__paint'), 'ellipsePaint');
  }
  if (evTarget.closest('.tool_rectangle')) {
    toggleTools(evTarget.closest('.tool_rectangle'), 'rectangle');
  }
  if (evTarget.closest('.tool_rectangle__paint')) {
    toggleTools(evTarget.closest('.tool_rectangle__paint'), 'rectanglePaint');
  }
  if (evType === 'input' && evTarget.closest('.tool_pen-size input')) {
    const inputRange = evTarget.closest('.tool_pen-size input');
    changePenSize(inputRange, that);
  }
  if (evTarget.closest('.color')) {
    evTarget.classList.add('selecting-color');
    document.querySelector('.tool_colors-select input').click();
  }
  if (evTarget.closest('.change-color')) {
    changeColor(that);
  }
  if (evType === 'change' && evTarget.closest('.tool_colors-select input')) {
    const inputColor = e.target.value;
    // eslint-disable-next-line no-use-before-define
    Tools.setColor(inputColor);
  }
}

export default class Tools {
  constructor(props) {
    this.props = props;
  }

  static create() {
    const tools = document.createElement('div');
    const paintBucket = document.createElement('button');
    const colorPicker = document.createElement('button');
    const move = document.createElement('button');
    const pen = document.createElement('button');
    const penSize = document.createElement('div');
    const eraser = document.createElement('button');
    const lightenDarken = document.createElement('button');
    const colorsSelect = document.createElement('div');
    const ellipse = document.createElement('button');
    const ellipsePaint = document.createElement('button');
    const rectangle = document.createElement('button');
    const rectanglePaint = document.createElement('button');
    const line = document.createElement('button');
    const clean = document.createElement('button');
    const hr = document.createElement('hr');
    penSize.innerHTML = '<span class="pen-size_view"><b>Pen size:</b> 1</span>'
                      + '<input type="range" max="5" min="1" step="1" value="1">';
    colorsSelect.innerHTML = '<div class="color color_primary"></div>'
                            + '<div class="color color_secondary"></div>'
                            + '<button class="change-color" title="shortcut: Y"></button>'
                            + '<input type="color">';
    paintBucket.setAttribute('title', 'shortcut: ""');
    colorPicker.setAttribute('title', 'shortcut: "Z"');
    move.setAttribute('title', 'shortcut: "M"');
    pen.setAttribute('title', 'shortcut: "P"');
    eraser.setAttribute('title', 'shortcut: "E"');
    lightenDarken.setAttribute('title', 'shortcut: "X"');
    ellipse.setAttribute('title', 'shortcut: "Q"');
    ellipsePaint.setAttribute('title', 'shortcut: "W"');
    rectangle.setAttribute('title', 'shortcut: "A"');
    rectanglePaint.setAttribute('title', 'shortcut: "S"');
    line.setAttribute('title', 'shortcut: "L"');
    clean.setAttribute('title', 'shortcut: "C"');
    tools.classList.add('tools');
    paintBucket.classList.add('tool', 'tool_paint-bucket');
    colorPicker.classList.add('tool', 'tool_color-picker');
    move.classList.add('tool', 'tool_move');
    lightenDarken.classList.add('tool', 'tool_lighten-darken');
    pen.classList.add('tool', 'tool_pen');
    penSize.classList.add('tool', 'tool_pen-size', 'outside');
    eraser.classList.add('tool', 'tool_eraser');
    line.classList.add('tool', 'tool_line');
    clean.classList.add('tool', 'tool_clean');
    ellipse.classList.add('tool', 'tool_ellipse');
    ellipsePaint.classList.add('tool', 'tool_ellipse__paint');
    rectangle.classList.add('tool', 'tool_rectangle');
    rectanglePaint.classList.add('tool', 'tool_rectangle__paint');
    colorsSelect.classList.add('tool', 'tool_colors-select', 'outside');
    tools.appendChild(penSize);
    tools.appendChild(pen);
    tools.appendChild(line);
    tools.appendChild(clean);
    tools.appendChild(eraser);
    tools.appendChild(ellipse);
    tools.appendChild(ellipsePaint);
    tools.appendChild(rectangle);
    tools.appendChild(rectanglePaint);
    tools.appendChild(hr);
    tools.appendChild(lightenDarken);
    tools.appendChild(move);
    tools.appendChild(paintBucket);
    tools.appendChild(colorPicker);
    tools.appendChild(colorsSelect);
    return tools;
  }

  render() {
    const { props } = this;
    const tools = Tools.create();
    tools.addEventListener('click', listenersFunc.bind(props), false);
    tools.addEventListener('input', listenersFunc.bind(props), false);
    tools.addEventListener('change', listenersFunc.bind(props), false);
    document.body.appendChild(tools);
  }

  static setColor(color) {
    const props = window.player;
    const selectedColorBlock = document.querySelector('.selecting-color') || document.querySelector('.color_primary');
    selectedColorBlock.style.backgroundColor = color;
    selectedColorBlock.classList.remove('selecting-color');
    if (selectedColorBlock.classList.contains('color_primary')) {
      props.canvasState.currentColor = color;
    }
  }
}
