export default class CanvasOptions {
  static setCorrectSize(canvasNode) {
    const canvasSize = canvasNode.getAttribute('height');
    const canvasListItem = canvasNode.parentNode;
    const canvasSizeNode = canvasListItem.querySelector('.canvas-options_size select');
    const lastSelectOption = canvasSizeNode.querySelector('[selected="true"]');
    const newSelectOptions = canvasSizeNode.querySelector(`[value="${canvasSize}"]`);
    lastSelectOption.setAttribute('selected', 'false');
    newSelectOptions.setAttribute('selected', 'true');
  }

  static drawCoordinates(x, y) {
    const activeCanvas = document.querySelector('.canvas-list__item.active');
    const coordinateX = activeCanvas.querySelector('.coordinate__x');
    const coordinateY = activeCanvas.querySelector('.coordinate__y');
    coordinateX.innerText = x;
    coordinateY.innerText = y;
  }

  static create() {
    const canvasOptions = document.createElement('div');
    const canvasCoordinates = document.createElement('div');
    const canvasSizeWrapper = document.createElement('div');
    const canvasSize = document.createElement('select');
    canvasCoordinates.innerHTML = '<b>Coordinates:</b> '
                                  + '<span>'
                                    + '[X: <span class="coordinate__x">0</span>, '
                                    + 'Y: <span class="coordinate__y">0</span>]'
                                  + '</span>';
    canvasSizeWrapper.innerHTML = '<b>Canvas size:</b> ';
    canvasSize.innerHTML = '<option value="32" selected="true">32x32</option>'
                          + '<option value="64">64x64</option>'
                          + '<option value="128">128x128</option>';
    canvasOptions.classList.add('canvas-options');
    canvasCoordinates.classList.add('canvas-options_coordinates');
    canvasSizeWrapper.classList.add('canvas-options_size');
    canvasSizeWrapper.appendChild(canvasSize);
    canvasOptions.appendChild(canvasCoordinates);
    canvasOptions.appendChild(canvasSizeWrapper);
    return canvasOptions;
  }
}
