import Header from './components/header/header';
import Tools from './components/tools/tools';
import FramesList from './components/frames-list/frames-list';
import Canvas from './screens/canvas/canvas';
import Preview from './screens/preview/preview';

function checkShortcuts(key) {
  const shortCuts = window.player.shortcuts;
  let selectedButton = null;
  Object.entries(shortCuts).forEach((item) => {
    const shortCut = item[1];
    if (shortCut[0] === key) {
      // eslint-disable-next-line prefer-destructuring
      selectedButton = shortCut[1];
    }
  });
  return selectedButton;
}

function setPenSize(command) {
  const range = document.querySelector('.tool_pen-size input');
  const view = document.querySelector('.pen-size_view');
  if (command === '.up') range.stepUp(1);
  if (command === '.down') range.stepDown(1);
  view.innerHTML = `<b>Pen size:</b> ${range.value}`;
}

function chooseButton(button) {
  if (button === '.up' || button === '.down') setPenSize(button);
  if (!document.querySelector(button)) return;
  document.querySelector(button).click();
}

function listenerFunc(e) {
  const that = this;
  if (e.type === 'keydown') {
    const pressKeys = e.key.toLowerCase();
    if (that.pressButtons !== pressKeys) that.pressButtons = pressKeys;
  }
  if (e.type === 'keyup') that.pressButtons = null;
  if (e.type === 'keypress') {
    const pressKey = e.key.toUpperCase();
    const button = checkShortcuts(pressKey);
    chooseButton(button);
  }
}

export default class Player {
  constructor() {
    this.selectedTools = {
      bucket: false,
      colorPicker: false,
      move: false,
      lightenDarken: false,
      pen: false,
      eraser: false,
      line: false,
      ellipse: false,
      ellipsePaint: false,
      rectangle: false,
      rectanglePaint: false,
    };
    this.canvasState = {
      isDraw: false,
      penSize: 1,
      currentColor: '#000000',
    };
    this.pressButtons = null;
    this.isMenuOpen = false;
    this.isOptionsOpen = false;
    this.animationFrames = [];
    this.currentFrame = null;
    this.countFrames = 0;
    this.selectedFPS = 4;
    this.frameOnPreview = 0;
    this.shortcuts = {
      penSizeUp: ['+', '.up'],
      penSizeDown: ['-', '.down'],
      addFrame: ['F', '.btn_new-frame'],
      removeFrame: ['R', '.frame__remove'],
      copyFrame: ['V', '.frame__copy'],
      pen: ['P', '.tool_pen'],
      line: ['L', '.tool_line'],
      clean: ['C', '.tool_clean'],
      eraser: ['E', '.tool_eraser'],
      ellipse: ['Q', '.tool_ellipse'],
      ellipsePaint: ['W', '.tool_ellipse__paint'],
      rectangle: ['A', '.tool_rectangle'],
      rectanglePaint: ['S', '.tool_rectangle__paint'],
      move: ['M', '.tool_move'],
      colorPicker: ['X', '.tool_lighten-darken'],
      lightenDarken: ['Z', '.tool_color-picker'],
      swapColor: ['Y', '.change-color'],
    };
  }

  render() {
    const header = new Header(this);
    const tools = new Tools(this);
    const framesList = new FramesList(this);
    const preview = new Preview(this);
    header.render();
    tools.render();
    framesList.render();
    Canvas.renderCanvasWrapper(this);
    preview.render();
    document.addEventListener('keydown', listenerFunc.bind(this));
    document.addEventListener('keyup', listenerFunc.bind(this));
    document.addEventListener('keypress', listenerFunc.bind(this));
  }
}
