import Preview from '../preview/preview';
import CanvasOptions from './canvas-options/canvas-options';
import Tools from '../../components/tools/tools';

function clearSelection() {
  if (window.getSelection) {
    if (window.getSelection().empty) {
      window.getSelection().empty();
    } else if (window.getSelection().removeAllRanges) {
      window.getSelection().removeAllRanges();
    }
  }
}

function createCursor(canvas) {
  const { selectedTools, canvasState } = window.player;
  const cursor = document.createElement('div');
  const canvasCSSHeight = canvas.clientHeight;
  const canvasSize = canvas.getAttribute('height');
  let flagPenSize = false;
  let { penSize } = canvasState;
  const ignoredTools = ['bucket', 'colorPicker', 'move', 'ellipse', 'ellipsePaint', 'rectangle', 'rectanglePaint'];
  Object.entries(selectedTools).forEach((_tool) => {
    if (ignoredTools.includes(_tool[0]) && !flagPenSize) {
      if (_tool[1] === true) flagPenSize = true;
    }
  });
  if (flagPenSize) penSize = 0;
  const cursorSize = (canvasCSSHeight / canvasSize) * penSize;
  cursor.classList.add('cursor');
  cursor.style.setProperty('height', `${cursorSize}px`);
  cursor.style.setProperty('width', `${cursorSize}px`);
  return cursor;
}

function renderCursor(canvas) {
  const cursor = createCursor(canvas);
  const activeCanvasList = document.querySelector('.canvas-list__item.active');
  activeCanvasList.appendChild(cursor);
}

function removeCursor() {
  const cursor = document.querySelector('.cursor');
  if (!cursor) return;
  const parent = cursor.parentNode;
  parent.removeChild(cursor);
}

function cursorMove(e) {
  const cursor = document.querySelector('.cursor');
  const mouse = { x: 0, y: 0, scale: 0 };
  mouse.x = e.offsetX;
  mouse.y = e.offsetY;
  cursor.style.top = `${mouse.y}px`;
  cursor.style.left = `${mouse.x}px`;
}

function setCanvasSize(node, size) {
  node.setAttribute('width', size);
  node.setAttribute('height', size);
}

function getImageData(canvas) {
  const ctx = canvas.getContext('2d');
  const canvasSize = canvas.getAttribute('height');
  return ctx.getImageData(0, 0, canvasSize, canvasSize);
}

function getCanvasColor(canvas) {
  const ctx = canvas.getContext('2d');
  const activeCanvas = document.querySelector('.canvas-list__item.active');
  const coordinateX = activeCanvas.querySelector('.coordinate__x').innerText;
  const coordinateY = activeCanvas.querySelector('.coordinate__y').innerText;
  const pointData = ctx.getImageData(coordinateX, coordinateY, 1, 1).data;
  let hexColor = '';
  pointData.forEach((item) => {
    const hex = Number(item).toString(16);
    hexColor += hex === '0' ? '00' : hex;
  });
  return `#${+hexColor === 0 ? 'ffffffff' : hexColor}`;
}

function copyImageInCanvas(imageData, cnvWrapSelector) {
  const canvasWrapper = document.querySelector(`${cnvWrapSelector}.active`);
  const canvas = canvasWrapper.querySelector('canvas');
  const ctx = canvas.getContext('2d');
  ctx.putImageData(imageData, 0, 0);
}

function changeCanvasSize(evTarget) {
  const canvasListItem = evTarget.closest('.canvas-list__item');
  const canvasListNumb = canvasListItem.getAttribute('data-canvas-number');
  const frame = document.querySelector(`.frame[data-number='${canvasListNumb}']`);
  const canvas = canvasListItem.querySelector('canvas');
  const canvasInFrame = frame.querySelector('canvas');
  const imageData = getImageData(canvas);
  setCanvasSize(canvasInFrame, evTarget.value);
  setCanvasSize(canvas, evTarget.value);
  copyImageInCanvas(imageData, '.frame');
  copyImageInCanvas(imageData, '.canvas-list__item');
}

function showCoordinates(e) {
  const canvasList = e.target.closest('.canvas-list');
  const canvasListCSS = getComputedStyle(canvasList);
  const canvasCSSHeight = e.target.clientHeight;
  const canvasSize = e.target.getAttribute('height');
  const leftCanvasList = parseInt(canvasListCSS.left, 10);
  const topCanvasList = parseInt(canvasListCSS.top, 10);
  const zoomCoordinates = canvasCSSHeight / canvasSize;
  let coordinatesX = Math.ceil((e.clientX - leftCanvasList) / zoomCoordinates) - 1;
  let coordinatesY = Math.ceil((e.clientY - topCanvasList) / zoomCoordinates) - 1;
  if (coordinatesX < 0) {
    coordinatesX = 0;
  } else if (coordinatesX > canvasSize - 1) {
    coordinatesX = canvasSize - 1;
  }
  if (coordinatesY < 0) {
    coordinatesY = 0;
  } else if (coordinatesY > canvasSize - 1) {
    coordinatesY = canvasSize - 1;
  }
  CanvasOptions.drawCoordinates(coordinatesX, coordinatesY);
}

function Draw(e, firstClick) {
  const { canvasState, selectedTools, pressButtons } = window.player;
  const evTarget = e.target;
  const ctx = evTarget.getContext('2d');
  const mouse = { x: 0, y: 0, scale: 0 };
  const canvasSize = evTarget.getAttribute('width');
  const canvasStyleWidth = evTarget.offsetWidth;
  mouse.scale = canvasStyleWidth / canvasSize;
  mouse.x = Math.ceil(e.offsetX / mouse.scale);
  mouse.y = Math.ceil(e.offsetY / mouse.scale);
  if (selectedTools.lightenDarken) {
    ctx.beginPath();
    mouse.x -= 1;
    mouse.y -= 0;
    const point = ctx.getImageData(mouse.x, mouse.y, canvasState.penSize, canvasState.penSize);
    point.data.forEach((item, index) => {
      let upValue = null;
      if (pressButtons === 'shift') {
        upValue = -Math.round(7 / canvasState.penSize);
        if (item === 0) upValue = 0;
      } else {
        upValue = Math.round(7 / canvasState.penSize);
        if (item === 255) upValue = 0;
      }
      if ((index + 1) % 4 !== 0) {
        point.data[index] = item + upValue;
      }
    });
    ctx.clearRect(mouse.x, mouse.y, canvasState.penSize, canvasState.penSize);
    ctx.putImageData(point, mouse.x, mouse.y);
  }
  if (selectedTools.move) {
    ctx.beginPath();
    ctx.clearRect(0, 0, canvasSize, canvasSize);
    const coordX = mouse.x - firstClick.x - (mouse.x < firstClick.x ? 1 : 1);
    const coordY = mouse.y - firstClick.y - (mouse.y < firstClick.y ? -0.5 : -1);
    ctx.putImageData(firstClick.canvasImage, coordX, coordY);
  }
  if (selectedTools.eraser) {
    ctx.beginPath();
    mouse.x -= 1;
    mouse.y -= 0;
    ctx.fillStyle = canvasState.currentColor;
    ctx.clearRect(mouse.x, mouse.y, canvasState.penSize, canvasState.penSize);
  }
  if (selectedTools.pen) {
    ctx.beginPath();
    ctx.fillStyle = canvasState.currentColor;
    mouse.x -= 1;
    mouse.y -= 0;
    ctx.fillRect(mouse.x, mouse.y, canvasState.penSize, canvasState.penSize);
  }
  if (selectedTools.line) {
    ctx.clearRect(0, 0, canvasSize, canvasSize);
    ctx.putImageData(firstClick.canvasImage, 0, 0);
    ctx.beginPath();
    mouse.x -= 0.5;
    mouse.y -= 0.5;
    ctx.fillStyle = '#00000000';
    ctx.strokeStyle = canvasState.currentColor;
    ctx.lineWidth = canvasState.penSize;
    ctx.moveTo(firstClick.x, firstClick.y);
    ctx.lineTo(mouse.x + 1, mouse.y + 1);
    ctx.stroke();
  }
  if (selectedTools.ellipse || selectedTools.ellipsePaint) {
    ctx.beginPath();
    ctx.clearRect(0, 0, canvasSize, canvasSize);
    ctx.putImageData(firstClick.canvasImage, 0, 0);
    if (selectedTools.ellipse) {
      mouse.x -= 0.5;
      mouse.y -= 0.5;
    }
    const radius = ((((mouse.x - firstClick.x) ** 2) + ((mouse.y - firstClick.y) ** 2)) ** 0.5);
    const radiusX = pressButtons === 'shift' ? radius : Math.abs(mouse.x - firstClick.x);
    let radiusY = pressButtons === 'shift' ? radiusX : Math.abs(mouse.y - firstClick.y);
    if (selectedTools.ellipse) {
      ctx.fillStyle = '#00000000';
      ctx.strokeStyle = canvasState.currentColor;
      ctx.lineWidth = canvasState.penSize;
      ctx.ellipse(firstClick.x, firstClick.y, radiusX, radiusY, 0, 2 * Math.PI, false);
      ctx.stroke();
    }
    if (selectedTools.ellipsePaint) {
      ctx.fillStyle = canvasState.currentColor;
      ctx.lineWidth = 0;
      if (pressButtons !== 'shift') {
        radiusY = (mouse.y < firstClick.y) ? radiusY : radiusY + 1;
      }
      ctx.ellipse(firstClick.x, firstClick.y, radiusX, radiusY, 0, 2 * Math.PI, false);
    }
  }
  if (selectedTools.rectangle || selectedTools.rectanglePaint) {
    ctx.beginPath();
    ctx.clearRect(0, 0, canvasSize, canvasSize);
    ctx.putImageData(firstClick.canvasImage, 0, 0);
    if (selectedTools.rectangle) {
      mouse.x -= 0.5;
      mouse.y -= 0.5;
    }
    let rectHeight = mouse.y - firstClick.y;
    let rectWidth = mouse.x - firstClick.x;
    if (selectedTools.rectangle) {
      if (pressButtons === 'shift') {
        rectWidth = (mouse.x < firstClick.x) ? -rectHeight : rectHeight;
        rectWidth = mouse.y < firstClick.y ? -rectWidth : rectWidth;
      }
      ctx.fillStyle = '#00000000';
      ctx.strokeStyle = canvasState.currentColor;
      ctx.lineWidth = canvasState.penSize;
      ctx.rect(firstClick.x, firstClick.y, rectWidth, rectHeight);
      ctx.stroke();
    }
    if (selectedTools.rectanglePaint) {
      rectHeight = mouse.y < firstClick.y ? rectHeight : rectHeight + 1;
      rectWidth = (mouse.x < firstClick.x) ? rectWidth - 1 : rectWidth;
      if (pressButtons === 'shift') {
        rectWidth = (mouse.x < firstClick.x) ? -rectHeight : rectHeight;
        rectWidth = mouse.y < firstClick.y ? -rectWidth : rectWidth;
      }
      ctx.fillStyle = canvasState.currentColor;
      ctx.lineWidth = 0;
      ctx.rect(firstClick.x, firstClick.y, rectWidth, rectHeight);
    }
  }
  ctx.fill();
}

function listenerFunc(e) {
  const { target: evTarget, type: evType } = e;
  const that = this;
  const { selectedTools } = that;
  switch (evType) {
    case 'click': {
      if (!evTarget.closest('canvas')) break;
      if (selectedTools.colorPicker) {
        const colorFromCanvas = getCanvasColor(evTarget);
        Tools.setColor(colorFromCanvas);
      }
      break;
    }
    case 'mousedown': {
      if (!evTarget.closest('canvas')) break;
      const {
        pen, line, eraser, ellipse, rectangle, ellipsePaint, rectanglePaint, move, lightenDarken,
      } = selectedTools;
      const firstClick = { x: null, y: null, canvasImage: getImageData(evTarget) };
      const activeCanvas = document.querySelector('.canvas-list__item.active');
      firstClick.x = +activeCanvas.querySelector('.coordinate__x').innerText;
      firstClick.y = +activeCanvas.querySelector('.coordinate__y').innerText;
      if (rectangle || ellipse || line) {
        firstClick.x -= 0.5;
        firstClick.y -= 0.5;
      }
      if (
        pen || line || eraser || ellipse
        || rectangle || ellipsePaint || rectanglePaint
        || move || lightenDarken
      ) {
        that.canvasState.isDraw = true;
        const canvas = evTarget.closest('canvas');
        canvas.onmousemove = (ev) => { Draw(ev, firstClick); };
      }
      break;
    }
    case 'mousemove': {
      if (!evTarget.closest('canvas')) break;
      showCoordinates(e);
      cursorMove(e);
      break;
    }
    case 'mouseover': {
      if (!evTarget.closest('canvas')) break;
      renderCursor(evTarget);
      break;
    }
    case 'mouseout':
    case 'mouseup': {
      if (evTarget.closest('canvas') && evType === 'mouseout') {
        removeCursor();
      }
      if (!that.canvasState.isDraw) break;
      const canvas = document.querySelector('.canvas-list__item.active canvas');
      canvas.onmousemove = null;
      const imageData = getImageData(canvas);
      copyImageInCanvas(imageData, '.frame');
      Preview.createAnimationFrames(that);
      Preview.startAnimationPreview(that);
      clearSelection();
      that.canvasState.isDraw = false;
      break;
    }
    case 'change': {
      changeCanvasSize(evTarget);
      Preview.createAnimationFrames(that);
      Preview.startAnimationPreview(that);
      break;
    }
    default: {
      break;
    }
  }
}

export default class Canvas {
  constructor(props) {
    this.props = props;
  }

  static create(number) {
    const canvas = document.createElement('canvas');
    const canvasOptions = CanvasOptions.create();
    const canvasWrapper = document.createElement('li');
    canvasWrapper.classList.add('canvas-list__item');
    setCanvasSize(canvas, 32);
    canvasWrapper.setAttribute('data-canvas-number', number);
    canvasWrapper.appendChild(canvas);
    canvasWrapper.appendChild(canvasOptions);
    return canvasWrapper;
  }

  static renderCanvasWrapper(props) {
    const canvasList = document.createElement('ul');
    canvasList.classList.add('canvas-list');
    canvasList.addEventListener('mousedown', listenerFunc.bind(props), false);
    canvasList.addEventListener('mousemove', listenerFunc.bind(props), false);
    canvasList.addEventListener('mouseup', listenerFunc.bind(props), false);
    canvasList.addEventListener('mouseout', listenerFunc.bind(props), false);
    canvasList.addEventListener('mouseover', listenerFunc.bind(props), false);
    canvasList.addEventListener('click', listenerFunc.bind(props), false);
    canvasList.addEventListener('change', listenerFunc.bind(props), false);
    document.body.appendChild(canvasList);
  }

  static copyCanvas(canvas, target) {
    const canvasSize = canvas.getAttribute('height');
    const ctx = canvas.getContext('2d');
    const imageData = ctx.getImageData(0, 0, canvasSize, canvasSize);
    const targetCtx = target.getContext('2d');
    targetCtx.putImageData(imageData, 0, 0);
    copyImageInCanvas(imageData, '.frame');
  }

  static setCanvasCorrectNumb() {
    if (!document.querySelectorAll('.canvas-list__item')) return;
    document.querySelectorAll('.canvas-list__item').forEach((item, index) => {
      item.setAttribute('data-canvas-number', index + 1);
    });
  }

  render() {
    const { props } = this;
    const canvasWrapper = Canvas.create(props.countFrames);
    const canvasList = document.querySelector('.canvas-list');
    canvasList.appendChild(canvasWrapper);
  }
}
